body {
    font-family: Lato, sans-serif;
}

table {
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    border-collapse: collapse;
    width: 40vw;
    height: 40vw;
    cursor: pointer;
    font-size: 4em;
}

table, th {
    border: 1px solid black;
}